* {
    margin: 0;
    padding: 0%;
    box-sizing: border-box;

}

.con {
    min-height: 100vh;
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("./images/mail.jpg") no-repeat center center/cover;
}

.con form {
    width: 670px;
    height: 400px;
    display: flex;
    justify-content: center;
    box-shadow: 1px 2px 59px #356a38;
    border-radius: 15px;
    background-color: rgb(48, 46, 46);
    backdrop-filter: blur(10px);
    flex-wrap: wrap;
}

.con form h1 {
    color: whitesmoke;
    font-weight: 500;
    margin-top: 20px;
    width: 500px;
    text-align: center;
    margin-bottom: 1%;
}

.con form input {
    width: 290px;
    height: 40px;
    padding-left: 10px;
    outline: none;
    font-size: 14px;
    margin-bottom: 10px;
    background: none;
    border-bottom: 2px solid white;
}

.con form input::placeholder {
    color: aliceblue;
}

.con form #lastname,
.con form #mobile {
    margin-left: 20px;
}

.con form h4 {
    color: aliceblue;
    font-weight: 300;
    width: 600px;
    margin-top: 20px;
}

.con form textarea {
    background: none;
    border: none;
    border-bottom: 2px solid wheat;
    font-weight: 300;
    font-size: 16px;
    padding: 10px;
    color: aliceblue;
    outline: none;
    min-width: 600px;
    max-width: 600px;
    min-height: 80px;
    max-height: 80px;
}

textarea::-webkit-scrollbar {
    width: 1rem;
}

textarea::-webkit-scrollbar-thumb {
    background-color: rgb(235, 235, 19);
    color: aliceblue;
}

.con form #button {
    border: none;
    background: whitesmoke;
    color: black;
    border-radius: 5px;
    margin-top: 20px;
    font-weight: 600;
    font-size: 20px;
    width: 100px;
    padding: 0;
    margin-right: 500px;
    margin-bottom: 30px;
    transition: 0.2s;
}

.con form #button:hover {
    opacity: 0.7;
}

