* {
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
}

.body {
    height: 140vh;
    background-color: rgb(214, 254, 255);
    width: 100%;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 10px;
    background-color: #eef4ee;
    width: 100%;
    box-shadow: 3px 3px 20px;
    padding: 0.2rem;
}

.nav-link {
    list-style: none;

}

.nav-link a {
    text-decoration: none;
}

.nav-link li {
    display: inline;
    padding: 0px 20px;
}

.nav-link li :hover {
    background-color: rgb(24, 23, 23);
    color: #e3ecf6;
    font-size: larger;
    font-weight: bold;
}

li {
    font-family: 'Courier New', Courier, monospace, sans-serif;
    font-weight: 200;
    font-size: 16px;
    color: black;
    text-decoration: none;
}

button {
    padding: 9px 24px;
    background-color: rgb(243, 163, 35);
    border-radius: 50px;
    cursor: pointer;
    color: #e3ecf6;
    margin-left: 735px;
    transition: all 0.3sec ease 0s;
    text-decoration: none;
    color: black;
}

button:hover {
    padding: 9px 24px;
    background-color: rgb(243, 163, 35);
    border-radius: 50px;
    cursor: pointer;
    color: #e3ecf6;
    margin-left: 735px;
    box-shadow: -1px 2px 10px;
    transition: all 0.3sec ease 0s;
}

.input-colourchoice {
    border: 1px solid silver;
    border-radius: 200px;
    padding: 1.1%;
    box-shadow: 2px 3px 4px;
    margin: 9px;
    width: 14%;
}

.input-colourchoice:hover {
    border: 1px solid silver;
    border-radius: 200px;
    padding: 1.1%;
    box-shadow: 2px 3px 4px green;
    margin-left: 6px;
    margin: 9px;
    width: 14%;
}

.slide {
    position: absolute;
    width: 70%;
    left: 15%;
    top: 20%;
    padding: 32px;
    box-shadow: 5px 2px 4px 1px;
    border-radius: 3%;
}

.img2 {
    width: 10rem;
    border-radius: 10%;
    box-shadow: 2px 3px 4px;
    float: right;
    margin-left: 2rem;
}

.import {
    height: 55vh;
}

.img2-p {
    text-align: -webkit-center;
    font-size: x-large;
}

/* -----------------Excellence.js */
.flex-con {
    width: 100%;
    height: 57vh;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.flex-box {
    width: 200px;
    height: 200px;
    background-color: rgb(46, 45, 45);
    color: aliceblue;
    font-size: 25px;
    text-align: center;
    line-height: 200px;
    border-radius: 20px;
    margin: 10px;
}

.flex-box:hover {
    width: 200px;
    height: 200px;
    background-color: rgb(247, 78, 78);
    color: rgb(14, 14, 15);
    font-size: 25px;
    text-align: center;
    line-height: 200px;
    border-radius: 20px;
    margin: 10px;
    border: 2px solid whitesmoke;
    cursor: cell;
}

.img2 {
    width: 27%;
}

.img3 {
    width: 17%;
    border-radius: 10%;
    box-shadow: 2px 3px 4px;
    margin-left: 10%;
}

.main {
    margin: 0;
    font-family: sans-serif;
    box-shadow: 2px 9px 4px;

}

.sec {
    background-color: rgb(215, 214, 214);
    display: flex;
    justify-content: space-between;
    padding: 40px;
    width: 60%;
    margin: 10rem auto 0 auto;
    margin: 1%;
    width: 99%;
    font-family: math;
    border-radius: 4%;
}

.sec:hover {
    background-color: rgb(194, 183, 183);
    display: flex;
    justify-content: space-between;
    padding: 40px;
    width: 60%;
    margin: 10rem auto 0 auto;
    margin: 1%;
    width: 99%;
    font-family: math;
    box-shadow: 2px 3px 4px solid;

}

.sec img {
    height: 250px;
    width: 254%;

}

.text {
    font-size: 20px;
}

.sec h1 {
    color: #77ff77;
}

.sec p {
    color: rgb(0, 0, 0);
    margin-right: 16px;
}

aside {
    text-align: unset;
